import Hello from './components/HelloWorld'
import Tab from './components/Tab'
import ChangeButton from './components/ChangeButton'
import Table from './components/Table'

export const routes = [
    {
        path: 'hellovue/tab', name: 'tab', component: Tab
    },
    {
        path: 'hellovue/hello', name: 'Hello', component: Hello
    },
    {
        path: 'hellovue/table', name: 'table', component: Table
    },
    {
        path: 'hellovue/button', name: 'button', component: ChangeButton
    }
]